
import Vue from 'vue';

export default Vue.extend({
  name: 'ManualCheckbox',
  props: {
    mode: {
      type: String,
      default: '',
    },
    ok: {
      type: Boolean,
      required: true,
    },
    ng: {
      type: Boolean,
      required: true,
    },
    toggle: {
      type: Function,
      required: true,
    },
    imgPath: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
});
